$linkColor: #1865f2;

html, body {
    width: 100%;
    margin: 0px;
    padding: 0px; }

main {
    position: absolute;
    top: 60px;
    left: 0px;
    right: 0px; }

.block {
    position: relative;
    width: 100%;
    font-family: sans-serif;

    h1 {
        font-size: 32px;
        font-weight: 900; }

    h3 {
        font-size: 16px;
        font-weight: 300; }

    p {
        font-size: 16px;
        font-weight: 300;
        text-align: justify; } }

.whiteContent {
    color: white; }

.scaleblock {
    position: relative;
    max-width: 1000px;
    margin: 0px auto;
    padding: 25px; }

.fullscale {
    position: absolute;
    left: 0px;
    top: 0px;
    bottom: 0px;
    right: 0px;
    width: 100%;
    overflow: hidden;
    display: flex;
    align-content: center;
    justify-content: center;

    * {
        width: 100%;
        object-fit: cover;
        filter: brightness(40%); } }

.layer2 {
    background: #f6f6f6;
    fill: #f6f6f6; }

.custom-shape-divider {
    line-height: 0; }

.custom-shape-divider {
    position: relative;
    display: block;
    width: calc(100%);
    height: 140px;
    transform: rotateY(180deg) rotateX(180deg); }

.courses {
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-items: center;
    row-gap: 35px;
    column-gap: 25px;
    text-decoration: none;

    a {
        text-decoration: none; }

    article {
        position: relative;
        width: 90%;
        display: block;
        padding: 25px;
        background: #eee;
        border-top: 5px solid #ddd;
        border-radius: 5px;
        padding-top: 40px;

        label {
            position: absolute;
            right: 10px;
            top: 10px;
            text-transform: uppercase;
            font-weight: 900;
            color: #bbb;
            font-size: 14px; }

        video, img {
            // border-radius: 0px
            width: 100%;
            margin: 0px; }

        // i
        //     position: absolute
        //     display: block
        //     left: -55px
        //     border-radius: 50%
        //     height: 24px
        //     width: 24px
        //     border: 5px solid #ddd
        //     background: #f6f6f6

        &:hover {
            border-top: 5px solid $linkColor;
            cursor: pointer; }

        h2 {
            font-weight: 900;
            color: #222; }

        p {
            font-weight: 300;
            color: #666;
            line-height: 1.6;
            text-align: left; }

        ul {
            position: absolute;
            display: table;
            width: 100%;
            left: 0px;
            right: 0px;
            padding: 0px;
            margin: 0px;
            bottom: 0px;
            table-layout: fixed;

            li {
                display: table-cell;
                color: #888;
                font-weight: 300;
                font-size: 14px;
                padding: 10px;
                text-align: center; } } } }

@media screen and ( max-width: 600px ) {
    .courses {
        grid-template-columns: 1fr; }

    .table {
        display: block;

        td {
            display: block;
            width: 100%;
            padding: 0px;

            figure {
                width: 80%;
                padding: 0px;
                marign: 0px auto; } } } }
